@media screen and (min-width: 768px) { #pricing-section .et_pb_pricing_table { width: 30% !important; } }

#pricing-section .et_pb_pricing_table .et_pb_pricing_content_top { border-bottom: 8px solid; height: 32px; margin-top: -48px; padding: 0 0 40px; }

#pricing-section .et_pb_pricing_table .et_pb_pricing_content_top .et_pb_et_price { color: #fff; font-family: 'AvenirNextRegular', sans-serif; font-size: 19px; display: inline-block; overflow: hidden; text-indent: -7px; }

@media screen and (max-width: 980px) { #pricing-section .et_pb_pricing_table .et_pb_pricing_content_top .et_pb_et_price { font-size: 14px; text-indent: -6px; } }

#pricing-section .et_pb_pricing_table:first-child .et_pb_pricing_heading { background-image: url("data:image/png;base64,"), linear-gradient(115deg, #6369e3 50%, #433ab9); background-position: -115px -65px, 0px 0; padding: 0; }

#pricing-section .et_pb_pricing_table:first-child .et_pb_pricing_heading h2 { margin-bottom: 0; }

#pricing-section .et_pb_pricing_table:first-child .et_pb_pricing_content_top { border-color: #a4a8ff; }

#pricing-section .et_pb_pricing_table:nth-child(2) .et_pb_pricing_heading { background-image: url("data:image/png;base64,"), linear-gradient(115deg, #6369e3 30%, #433ab9); background-position: -70px -90px, 0px 0; }

#pricing-section .et_pb_pricing_table:nth-child(2) .et_pb_pricing_content_top { border-color: #7dd274; }

#pricing-section .et_pb_pricing_table:last-child .et_pb_pricing_heading { background-image: url("data:image/png;base64,"), linear-gradient(125deg, #6369e3, #433ab9 100%); background-position: -30px -135px, 0px 0; }

#pricing-section .et_pb_pricing_table:last-child .et_pb_pricing_content_top { border-color: #ff722b; }

#pricing-section .et_pb_pricing_heading { align-items: center; background-repeat: no-repeat; display: flex; flex-direction: column; height: 214px; justify-content: center; margin-bottom: 0; padding-bottom: 40px; }

#pricing-section .et_pb_pricing_heading .et_pb_pricing_title { font-family: 'AvenirNextDemiBold', sans-serif; font-size: 55px; margin-bottom: 5px; text-transform: uppercase; }

@media screen and (max-width: 1100px) { #pricing-section .et_pb_pricing_heading .et_pb_pricing_title { font-size: 50px; } }

@media screen and (max-width: 980px) { #pricing-section .et_pb_pricing_heading .et_pb_pricing_title { font-size: 40px; } }

#pricing-section .et_pb_pricing_heading .et_pb_best_value { font-family: 'AvenirNextDemiBold', sans-serif; font-size: 39px; }

@media screen and (max-width: 1100px) { #pricing-section .et_pb_pricing_heading .et_pb_best_value { font-size: 34px; } }

@media screen and (max-width: 980px) { #pricing-section .et_pb_pricing_heading .et_pb_best_value { font-size: 30px; } }

#pricing-section .et_pb_pricing_heading .et_pb_best_value:before { content: '$'; font-size: 24px; padding-right: 5px; }

@media screen and (max-width: 980px) { #pricing-section .et_pb_pricing_heading .et_pb_best_value:before { font-size: 21px; } }

#pricing-section .et_pb_pricing_content { font-family: 'AvenirNextRegular', sans-serif; padding: 0; }

#pricing-section ul.et_pb_pricing li { align-items: center; color: #1d2631; display: flex; font-size: 17px; font-weight: 600; height: 53px; justify-content: center; line-height: 17px; padding: 0 10px; }

#pricing-section ul.et_pb_pricing li:nth-child(even) { background: #f6f9fe; }

#pricing-section ul.et_pb_pricing li:nth-child(odd) { background: #fff; }

@media screen and (max-width: 980px) { #pricing-section ul.et_pb_pricing li { font-size: 15px; } }

@media screen and (min-width: 768px) and (max-width: 980px) { #pricing-section ul.et_pb_pricing li { height: 60px; } }

#pricing-section ul.et_pb_pricing li span span { color: #79899c; }

#pricing-section ul.et_pb_pricing li span.block { display: block; }

#pricing-section .et_pb_button_wrapper { padding: 30px 0 0; }

#pricing-section .et_pb_pricing_table_button { height: 41px; line-height: 33px !important; min-width: 180px; text-decoration: none; }

#main-content #pricing-section .et_pb_pricing_table_button:hover { background: #6269e2 !important; color: #fff !important; }

#pricing-section a { text-decoration: underline; }

#pricing-section a:hover { text-decoration: none; }
